* {
  font-family: 'Death Note Font', sans-serif;
  transition: background-color 0.5s ease-in-out, color 0.5s ease-in-out; /* Smooth transition for both background and text color */
}
html, body {
  height: 100%;
  margin: 0;
  color: white;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  background-image: url('./assets/bg.png');
  background-repeat: no-repeat;
  background-size: 100% 100%; /* Stretch the image to fill the element */
  background-position: center; /* Centers the image */
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 50px;
  font-size: 75px;
}

.full-width {
  width: 100%;
}

.center {
  display: flex;
  justify-content: center;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.info {
  top: 0;
  left: 0;
  margin-left: 120px;
  margin-top: 100px;
  color: black;
  font-weight: bold;
}

.address {
  font-size: 40px;
  margin: 0;
}

.amount {
  font-size: 20px;
  margin: 0;
}

input {
  margin-top: 20px;
  margin-bottom: 20px;
  border: none;
  background-color: transparent;
  border-bottom: 2px dashed rgba(0, 0, 0, 0.568);
  font-size: 15px;
  padding: 5px;
  width: 100%;
}

input:focus {
  outline: none;
}

.info-right {
  top: 0;
  right: 0;
  margin-right: 120px;
  margin-top: 100px;
  color: black;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-items: center;
  align-items: center;
  width: 300px;
  height: 420px;
}

button {
  z-index: 99999999999;
  background-color: transparent;
  color: white;
  border: none;
  font-size: 25px;
  cursor: pointer;
}

button:hover {
  font-weight: bold;
}

.button-left-margin {
  margin-right: 50px;
}

.button-right-margin {
  padding-left: 200px;
}

.circle {
  margin-top: 50px;
  background-color: white;
  border-radius: 100%;
  width: 750px !important;
  height: 750px !important;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.2; /* Set initial opacity */
  filter: blur(10px); /* Add blur */
  animation: scale-blur .4s infinite alternate ease-in-out; /* Apply animation */
}

.circle1 {
  margin-top: 50px;
  background-color: white;
  border-radius: 100%;
  width: 750px !important;
  height: 750px !important;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.2; /* Set initial opacity */
  filter: blur(10px); /* Add blur */
  animation: scale-blur .25s infinite alternate ease-in-out; /* Apply animation */
}

.red-bg {
  background-color: red;
}
.white-bg {
  background-color: white;
}
.red {
  color: red;
}
.white {
  color: white;
}

@keyframes scale-blur {
  0% {
    transform: translate(-50%, -50%) scale(1); /* Original size */
    opacity: 0.2;
    filter: blur(100px);
  }
  100% {
    transform: translate(-50%, -50%) scale(1.2); /* Scaled up */
    opacity: 0.5;
    filter: blur(100px);
  }
}

.margin-top {
  margin-top: 75px;
  margin-right: 75px;
  animation: scaling .6s infinite alternate ease-in-out; /* Apply animation */
}

@keyframes scaling {
  0% {
    transform: scale(.9);
  }
  100% {
    transform: scale(1);
  }
}

.clickme {
  top: 40%;
  right: 38%;
  font-size: 30px;
  font-weight: bold;
  cursor: pointer;
  rotate: 20deg;
}